import {useMemo} from "react";

/**
 * @param obj  ,   ,
 * @param objKey  ,   ,
 * @param criterion -  /   
 * @returns script version
 */

export const useFiltredObj = <T>(
  obj: T[] | null,
  objKey: keyof T,
  criterion: string,
) => {
  return useMemo(() => {
    if (obj && criterion) {
      return obj.filter(el => String(el[objKey]).includes(criterion));
    }
    return obj;
  }, [obj, objKey, criterion]);
};
